p {
  font-size: 1.125rem;
  margin: 0 0.625rem;
}

a:visited {
  color: #fff;
  text-decoration: none;
}

.container-header {
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-style: normal;
  letter-spacing: normal;
}

.container-header .h1header {
  font-size: 1rem;
  padding: 1.25rem;
  color: #fff;
  margin-top: 0;
}

.container-header .h1header a:hover {
  color: #000;
  transition: all 0.7s ease;
}

.header {
  position: fixed;
  padding: 0px;
  width: 100vw;
  margin: auto;
}

.header .group {
  opacity: 0.95;
  background-color: #099d8d;
  text-align: center;
}

.header .main-menu {
  padding: 0.9375rem;
  opacity: 0.9;
  background-color: #14d9c4;
  z-index: 10;
  text-align: center;
}

.header .main-menu ul li {
  display: inline;
}

.header .main-menu ul li a {
  font-size: 0.9375rem;
  font-family: "Rubik", sans-serif;
  font-weight: 500;
  font-style: normal;
  color: #fff;
  padding: 0.5rem;
}

.header .main-menu a:hover {
  color: #000;
  transition: all 0.7s ease;
}

.hero {
  background-color: #099d8d;
  background-image: url(/_src/assets/images/IMG_20200207_175627.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 421px;
 /*width: 100vw;*/
  display: flex;
  position: relative;
  z-index: -1;
}

.hero .textbox {
  box-sizing: border-box;
  width: 290px;
  height: 80px;
  background-color: #fff;
  color: #000;
  font-size: 1.125rem;
  font-family: "Open Sans", sans-serif;
  position: absolute;
  bottom: -40px;
  left: calc(50% - 145px);
}

.hero .textbox div {
  height: 50%;
}

.whitebox p {
  position: relative;
  text-align: left;
  padding: 0.9375rem;
}/* A partir de aqui es responisve *//* @import 'layouts/aboutus'; */.strengths {
  background-color: #b8b8b9;
  display: flex;
  justify-content: center;
  position: relative;
}

.strengths .strengths__triangle {
  width: 1.875rem;
  height: 1.875rem;
  background-color: white;
  transform: rotate(45deg);
  position: absolute;
  top: -0.9375rem;
}

.strengths h2 {
  color: #099d8d;
  font-size: 24px;
  font-family: "Rubik", sans-serif;
  font-weight: 31.25rem;
  margin: 0.9375rem;
}

.strengths p {
  padding-bottom: 1.875rem;
  font-size: 16px;
  font-family: "Open Sans", sans-serif;
  color: #54585a;
  text-align: center;
  line-height: 1.5rem;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  padding: 30px;
  color: #b8b8b9;
  font-size: var(--text-content-size);
  text-align: center;
  font-family: var(--opensans);
  border-width: 100%;
 /* no sé hacerlo de otra manera */;
}

.footer .footer-nav {
  margin-left: -35px;
}

.footer .footer-nav li {
  list-style-type: none;
  padding: 5px;
}

.footer .footer-nav a {
  color: #54585a;
  font-weight: bold;
  text-decoration: none;
}

.footer .footer-nav a:hover {
  color: var(--black);
  transition: all 0.7s ease;
}

.footer .footer-logo img {
  width: 120px;
