 */@media all and (min-width: 768px) {
  .container-header {
    position: fixed;
    width: 100vw;
    background-color: #099d8d;
    opacity: 0.95;
    height: 60px;
  }

  .container-header h1 {
    padding: 1.25rem 0px;
  }

  .header {
    position: relative;
    display: flex;
    flex-direction: row;
    opacity: 0.95;
    justify-content: space-between;
    align-items: stretch;
    max-width: 700px;
  }

  .header .main-menu {
    background-color: transparent;
    padding: 1.8125rem 0px;
  }

  .header .main-menu .group {
    background-color: transparent;
  }

  .hero .whitebox {
    position: relative;
    height: 421px;
  }

  .hero .whitebox .textbox {
    left: 15px;
    bottom: 29px;
    opacity: 80%;
    padding: 0.9375rem 1.875rem;
    width: 441px;
  }

  .strengths .strengths__box {
    display: flex;
  }

  .strengths div {
    max-width: 50%;
    padding: 15px 0px;
  }

  .strengths h2 {
    margin: 0px;
    padding: 15px;
  }

  .strengths p {
    margin: 0 15px;
  }

  .footer {
    height: auto;
    flex-direction: row;
    text-align: right;
    position: relative;
    justify-content: space-between;
    padding: 3% 6%;
  }

  .footer .footer-logo {
    position: absolute;
    left: 5.6%;
    top: 30%;
    z-index: -1;
  }

  .footer .footer-logo img {
    width: 100px;
  }
}

@media all and (min-width: 1200px) {
  .header {
    max-width: 900px;
  }

  .hero {
    height: 550px;
  }

  .hero .whitebox {
    height: 550px;
  }

  .hero .whitebox p {
    font-size: 1.25rem;
  }

  .hero .whitebox p .textbox {
    width: 574px;
    height: 90px;
  }
}
