a:visited {
  color: $menuColor;
  text-decoration: none;
}

.container-header {
  font-family: $fontFamilyTitle;
  font-weight: 500;
  font-style: normal;
  letter-spacing: normal;

  & .h1header {
    font-size: rem(16);
    padding: rem(20);
    color: $menuColor;
    margin-top: 0;

    & a:hover {
      color: $bodyImportantColor;
      transition: all 0.7s ease;
    }
  }
}

.header {
  position: fixed;
  padding: 0px;
  width: 100vw;
  margin: auto;

  .group {
    opacity: 0.95;
    background-color: $headerColor;
    text-align: center;
  }

  .main-menu {
    padding: rem(15);
    opacity: 0.9;
    background-color: $subHeaderColor;
    z-index: 10;
    text-align: center;

    & ul li {
      display: inline;

       & a {
        font-size: rem(15);
        font-family: $fontFamilyTitle;
        font-weight: 500;
        font-style: normal;
        color: $menuColor;
        padding: rem(8);
      }
    }

    & a:hover {
      color: $bodyImportantColor;
      transition: all 0.7s ease;
    }
  }
}

