// Aqui empieza el hero

.hero {
  background-color: $headerColor;
  background-image: url(/_src/assets/images/IMG_20200207_175627.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 421px;
  /*width: 100vw;*/
  display: flex;
  position: relative;
  z-index: -1;

  .textbox {
    box-sizing: border-box;
    width: 290px;
    height: 80px;
    background-color: $menuColor;
    color: $bodyImportantColor;
    font-size: rem(18);
    font-family: $fontFamilyBody;
    position: absolute;
    bottom: -40px;
    left: calc(50% - 145px);

    & div {
      height: 50%;
    }
  }
}
.whitebox p {
  position: relative;
  text-align: left;
  padding: rem(15);
}

/* A partir de aqui es responisve */

@include forTablet {
  .container-header {
    position: fixed;
    width: 100vw;
    background-color: $headerColor;
    opacity: 0.95;
    height: 60px;

    & h1 {
      padding: rem(20) 0px;
    }
  }
  .header {
    position: relative;
    display: flex;
    flex-direction: row;
    opacity: 0.95;
    justify-content: space-between;
    align-items: stretch;
    max-width: 700px;

    .main-menu {
      background-color: transparent;
      padding: rem(29) 0px;

      .group {
        background-color: transparent;
      }
    }
  }

  .hero {
    .whitebox {
      position: relative;
      height: 421px;
      .textbox {
        left: 15px;
        bottom: 29px;
        opacity: 80%;
        padding: rem(15) rem(30);
        width: 441px;
      }
    }
  }
}

@include forPc {
  .header {
    max-width: 900px;
  }
  .hero {
    height: 550px;

    .whitebox {
      height: 550px;
      & p {
        font-size: rem(20);
        .textbox {
          width: 574px;
          height: 90px;
        }
      }
    }
  }
}
